import React, { Component } from "react";
import { Button, Header, Icon, Modal, Form, Message } from "semantic-ui-react";
import web3 from "../web3";
import trojanSecret from "../trojanSecret";

export default class ListPlayers extends Component {
  state = {
    modalOpen: false,
    numPlayers: "",
    message: "",
    errorMessage: ""
  };

  async componentDidMount() {
    const numPlayers = await trojanSecret.methods.memberCount().call();
    this.setState({ numPlayers });
  }
  handleOpen = () => this.setState({ modalOpen: true });

  handleClose = () => this.setState({ modalOpen: false });

  render() {
    return (
      <Modal
        trigger={
          <Button color="purple" onClick={this.handleOpen}>
            List all Players
          </Button>
        }
        open={this.state.modalOpen}
        onClose={this.handleClose}
      >
        <Header icon="browser" content="List All Players" />
        <Modal.Content>
          <h3>
            It will be your job to find a way to list all the player names.
            <br />
            <br />
            But I can tell you this much: {this.state.numPlayers} names have
            been registered to play this game.
          </h3>
        </Modal.Content>
        <Modal.Actions>
          <Button color="red" onClick={this.handleClose} inverted>
            <Icon name="cancel" /> Close
          </Button>
        </Modal.Actions>
      </Modal>
    );
  }
}
